.footer {
    .flex-justify-space-between;
    background: @lightgrey;
    padding-left: 5%;
    padding-right: 5%;
}

.footer__bio {
    width: 40%;
    display: flex;
    align-items: center;
    img {
        margin-right: 20px;
        border: 3px solid white;
    }
}

.footer__socials ul{
    width: 30%;
    text-align: center;
}

.footer__socials ul{
    margin: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .svg-inst {
        position: absolute;
        width: 40px;
        height: 40px;
        margin-left: -65px;
        margin-top: 20px;
    }
    .svg-teleg {
        position: absolute;
        width: 50px;
        height: 50px;
        margin-left: -70px;
        margin-top: 20px;
    }
    .paragraph {
        padding: 10px;
        text-decoration: underline;
        color: white;
    }
}

.footer__about {
    width: 30%;
    li {
        margin-left: -40px;
    }
}