@font-face {
  font-family: 'Cuprum-regular';
  src: url('../fonts/cuprum_regular_macroman/Cuprum-Regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Cuprum-bold';
  src: url('../fonts/cuprum_bold_macroman/Cuprum-Bold-webfont.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Cuprum-italic';
  src: url('../fonts/cuprum_italic_macroman/Cuprum-Italic-webfont.woff') format('woff');
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: 'PressStart2p';
  src: url('../fonts/press_start_2p/PressStart2P-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'LDS';
  src: url('../fonts/LDS/17690.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Oswald';
  src: url(../fonts/Oswald_extra_light/17222.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
body {
  background-color: black;
  color: white;
}
li {
  list-style-type: none;
}
p,
h1,
h2,
h3,
h4,
a {
  font-family: 'Oswald';
  letter-spacing: 0.15em;
}
a {
  text-decoration: none;
}
.down-arrow-svg {
  width: 50px;
  fill: white;
}
.flex-justify-center {
  display: flex;
  flex-basis: 0;
  justify-content: center;
}
.flex-justify-end {
  display: flex;
  flex-basis: 0;
  justify-content: end;
}
.flex-justify-start {
  display: flex;
  flex-basis: 0;
  justify-content: start;
}
.flex-justify-space-between {
  display: flex;
  flex-basis: 0;
  justify-content: space-between;
}
.footer {
  display: flex;
  flex-basis: 0;
  justify-content: space-between;
  background: #181818;
  padding-left: 5%;
  padding-right: 5%;
}
.footer__bio {
  width: 40%;
  display: flex;
  align-items: center;
}
.footer__bio img {
  margin-right: 20px;
  border: 3px solid white;
}
.footer__socials ul {
  width: 30%;
  text-align: center;
}
.footer__socials ul {
  margin: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.footer__socials ul .svg-inst {
  position: absolute;
  width: 40px;
  height: 40px;
  margin-left: -65px;
  margin-top: 20px;
}
.footer__socials ul .svg-teleg {
  position: absolute;
  width: 50px;
  height: 50px;
  margin-left: -70px;
  margin-top: 20px;
}
.footer__socials ul .paragraph {
  padding: 10px;
  text-decoration: underline;
  color: white;
}
.footer__about {
  width: 30%;
}
.footer__about li {
  margin-left: -40px;
}
.header {
  background: #181818;
}
.header .wrapper {
  display: flex;
  justify-content: space-around;
}
.header .paragraph {
  margin-top: auto;
  margin-bottom: auto;
  font-size: 230%;
}
.header__menu {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.header__menu-item {
  width: 20%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-basis: auto;
  background-color: black;
  border: 1px solid white;
  font-weight: bold;
}
.header__menu-item a {
  color: white;
}
.content {
  display: flex;
  flex-basis: 0;
  justify-content: center;
}
h1 {
  text-align: center;
}
.content__elem .paragraph {
  text-align: center;
}
.content__elem:nth-child(1) {
  padding-bottom: 90px;
  padding-top: 90px;
}
.content__elem a {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.content__elem a svg {
  align-self: center;
}
.gallery {
  display: flex;
  flex-basis: 0;
  justify-content: space-between;
  background: #181818;
}
.main__section > h2 {
  width: 100%;
  text-align: center;
}
.graphlist {
  padding: 5%;
}
.graphlist__item-wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  display: flex;
  flex-basis: 0;
  justify-content: space-between;
}
.progress-outer {
  width: 50%;
  height: 14px;
  background: #181818;
  align-self: center;
  margin-left: 20px;
}
.progress-86 {
  width: 0%;
  height: 14px;
  background: #ffcd33;
  animation-name: loading86;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
@keyframes loading86 {
  from {
    width: 0%;
  }
  to {
    width: 86%;
  }
}
.progress-30 {
  width: 0%;
  height: 14px;
  background: #ffcd33;
  animation-name: loading30;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
@keyframes loading30 {
  from {
    width: 0%;
  }
  to {
    width: 30%;
  }
}
.progress-80 {
  width: 0%;
  height: 14px;
  background: #ffcd33;
  animation-name: loading80;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
@keyframes loading80 {
  from {
    width: 0%;
  }
  to {
    width: 80%;
  }
}
.progress-84 {
  width: 0%;
  height: 14px;
  background: #ffcd33;
  animation-name: loading84;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
@keyframes loading84 {
  from {
    width: 0%;
  }
  to {
    width: 84%;
  }
}
.portfolio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.portfolio__inner {
  display: flex;
}
.portfolio__inner > .paragraph {
  margin-right: 30px;
}
.lds-spinner {
  color: official;
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-spinner div {
  transform-origin: 32px 32px;
  animation: lds-spinner 1.2s linear infinite;
}
.lds-spinner div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 29px;
  width: 5px;
  height: 14px;
  border-radius: 20%;
  background: #fff;
}
.lds-spinner div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -1.1s;
}
.lds-spinner div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -1s;
}
.lds-spinner div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}
.lds-spinner div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}
.lds-spinner div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}
.lds-spinner div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}
.lds-spinner div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}
.lds-spinner div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}
.lds-spinner div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}
.lds-spinner div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}
.lds-spinner div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}
.lds-spinner div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
}
@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
